import Vue from "vue";
import Vuex from "vuex";
import categorys from "./modules/categorys";
import events from "./modules/events";
import setting from "./modules/setting";
import blocks from "./modules/blocks";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loading: true
  },
  getters: {
    loading(state) {
      return state.loading;
    }
  },
  mutations: {
    onLoaded(state) {
      state.loading = false;
    }
  },
  actions: {
    //读取数据
    initDate({ dispatch, commit }) {
      const promise = [];
      promise.push(dispatch("initCategorys"));
      promise.push(dispatch("initEvents"));
      promise.push(dispatch("initSettings"));

      Promise.all(promise).then(() => {
        commit("onLoaded");
      });
    }
  },
  modules: {
    categorys,
    events,
    setting,
    blocks
  }
});
